/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

/*
    Extra Resetting
*/

* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

body {
  background: -webkit-gradient(linear, left top, left bottom, from(#6f86d6), to(#48c6ef));
  background: -webkit-linear-gradient(#6f86d6, #48c6ef);
  background: -o-linear-gradient(#6f86d6, #48c6ef);
  background: linear-gradient(#6f86d6, #48c6ef);
  background-attachment: fixed;
  background-size: cover;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  font-family: "Roboto", sans-serif;
  height: 100vh;
  line-height: 1.75;
  /*overflow: auto;*/
  width: 100vw;
}

#root {
  height: 100%;
}

.page {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  width: 100%;
  height: 100vh;
}

/*
***** HeaderBar *****
*/

.headerToolbar {
  background: #fff !important;
  display: -ms-flexbox !important;
  display: flex !important;
  -ms-flex-flow: column nowrap !important;
      flex-flow: column nowrap !important;
  padding: 0px 24px !important;
  vertical-align: baseline !important;
  height: 120px !important;
}

.headerTitleWrapper {
  display: -ms-flexbox !important;
  display: flex !important;
  -ms-flex-flow: row nowrap !important;
      flex-flow: row nowrap !important;
  vertical-align: baseline !important;
  -ms-flex-preferred-size: 45%;
      flex-basis: 45%;
  -ms-flex-pack: start !important;
      justify-content: flex-start !important;
}

.headerButtonWrapper {
  width: 100% !important;
  display: -ms-flexbox !important;
  display: flex !important;
  -ms-flex-flow: row nowrap !important;
      flex-flow: row nowrap !important;
  margin-left: 1rem !important;
  vertical-align: baseline !important;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: end !important;
      justify-content: flex-end !important;
  -ms-flex-preferred-size: 45%;
      flex-basis: 45%;
}

.headerLogo {
  height: 36px !important;
  margin: 0 24px !important;
}

.headerFilter {
  margin: 0 0 1.5rem 2rem !important;
}

@media only screen and (min-width: 600px) {
  .headerToolbar {
    height: 64px !important;
    -ms-flex-flow: row nowrap !important;
        flex-flow: row nowrap !important;
    /* justify-content:; */
  }
}

@media only screen and (min-width: 600px) {
  .single-item-card {
    width: 44%;
    margin: 5px 2%;
  }
}

@media only screen and (min-width: 900px) {
  .single-item-card {
    width: 30.33%;
    margin: 5px 1.5%;
  }
}

/*
***** Item Card List - Item Cards *****
*/

.itemsContainer {
  width: 100%;
}

.itemCard {
  width: 98%;
  margin: 5px 1%;
}

.react-gravatar {
  border-radius: 50%;
}

@media only screen and (min-width: 600px) {
  .itemCard {
    width: 44%;
    margin: 10px 2%;
  }
}

@media only screen and (min-width: 900px) {
  .itemCard {
    width: 30.33%;
    margin: 10px 1.5%;
  }
}

