.page {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: center !important;
      align-items: center !important;
  -ms-flex-line-pack: center !important;
      align-content: center !important;
  -ms-flex-pack: center !important;
      justify-content: center !important;
  background: transparent;
  width: 100%;
  height: 100%;
  max-width: 100%;
}

.cardContainer {
  width: 420px;
  max-width: 95%;
  min-width: 320px;
}

.formContainer {
  padding: 1rem 4rem 3.25rem 4rem;
}

.logo {
  margin-bottom: 1rem;
  margin-top: -97px;
  position: relative;
  z-index: 1000;
}

.enterButton {
  margin: 2.25rem 0 0 0;
}

.page .loginFormError {
  position: absolute;
  bottom: 1rem;
  text-align: center;
  padding: 1rem 0;
  color: red;
  width: 290px;
  font-size: 12px;
