.page .bottomLeft {
  bottom: 0;
  left: 0;
  margin-bottom: -10px;
  overflow: hidden;
  position: absolute;
}

.page .topRight {
  -webkit-animation: parallax-clouds 60s infinite alternate ease-in-out;
          animation: parallax-clouds 60s infinite alternate ease-in-out;
  position: absolute;
  top: 0;
  right: 0;
}

@-webkit-keyframes parallax-clouds {
  0% {
    -webkit-transform: translate3d(0px, 0px, 0px);
            transform: translate3d(0px, 0px, 0px);
  }
  50% {
    -webkit-transform: translate3d(50px, 0px, 0px);
            transform: translate3d(50px, 0px, 0px);
  }
  100% {
    -webkit-transform: translate3d(-50px, 0px, 0px);
            transform: translate3d(-50px, 0px, 0px);
  }
}

@keyframes parallax-clouds {
  0% {
    -webkit-transform: translate3d(0px, 0px, 0px);
            transform: translate3d(0px, 0px, 0px);
  }
  50% {
    -webkit-transform: translate3d(50px, 0px, 0px);
            transform: translate3d(50px, 0px, 0px);
  }
  100% {
    -webkit-transform: translate3d(-50px, 0px, 0px);
            transform: translate3d(-50px, 0px, 0px);
  }
}



